import React, { forwardRef, useEffect, useImperativeHandle, useRef } from "react";
import cytoscape from "cytoscape";
import svg from 'cytoscape-svg';
import { store } from "../../app/store";
import saveAsImage from "../../app/IO/saveImage";

type CytoscapeProps = {
    elements?: (cytoscape.NodeDefinition | cytoscape.EdgeDefinition)[];
}

export type CytoscapeHandle = {
    draw: () => void;
    takeImage: () => void;
    toggleGrabbable: () => void;
}
const ReactionNetwork: React.ForwardRefRenderFunction<CytoscapeHandle, CytoscapeProps> = (props, ref) => {
    const container = useRef<HTMLDivElement>(null);
    const graph = useRef<cytoscape.Core>();
    const layout = useRef<cytoscape.Layouts>();

    const nodeStyle: cytoscape.Stylesheet[] = [
        {
            selector: 'node',
            style: {
                'shape': 'rectangle',
            }
        },
        {
            selector: 'node[label]',
            style: {
                'background-color': '#f00',
                'background-opacity': 0,
                'shape': 'rectangle',
                // 'width': '50px',
                // 'height': '30px',
                'label': 'data(id)',
                'text-valign': 'top',
                'text-halign': 'center',
                'text-opacity': 0,
                'border-opacity': 0,
            }
        },
        {
            selector: 'node[color]',
            style: {
                'background-color': '#fff',
                'shape': 'rectangle',
                'label': 'data(id)',
                'width': '55px',
                'height': '30px',
                'color': 'data(color)',
                'text-valign': 'center',
                'text-halign': 'center',
                'border-color': '#000',
                'border-width': 2,
                'border-style': 'solid',
            }
        },
    ];
    const edgeStyle: cytoscape.Stylesheet[] = [{
        selector: 'edge',
        style: {
            'width': 3,
            'line-color': '#ccc',
            'label': 'data(label)',
            'curve-style': 'bezier',
            'control-point-step-size': 50,
            'source-endpoint': 'inside-to-node',
        }
    }, {
        selector: 'edge[products]',
        style: {
            'target-arrow-shape': 'triangle-backcurve',
        }
    }, {
        selector: 'edge[educts]',
        style: {
            'target-endpoint': 'inside-to-node',
        }
    }, {
        selector: 'edge[outflow]',
        style: {
            'target-arrow-shape': 'triangle-backcurve',
        }
    }];

    const style: cytoscape.Stylesheet[] = [...nodeStyle, ...edgeStyle];

    useEffect(() => {
        const _elements = store.getState().app_reducer.networkGraph;
        if(_elements !== []) {
            try {
                if (!graph.current) {
                    cytoscape.use(svg);
                    graph.current = cytoscape({
                        elements: _elements,
                        style: style,
                        container: container.current
                    });
                }
            } catch (error) {
                console.error(error);
            }
            if (graph.current) {
                if (layout.current) {
                    layout.current.stop();
                } else {
                    redrawGraph(_elements);
                }
                graph.current.addListener('layoutstop', () => {
                    redrawGraph(_elements);
                });
            }else {

            }
        }
        
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useImperativeHandle(ref, ()=> ({
        draw() {
            const _elements = store.getState().app_reducer.networkGraph;
            try {
                if (!graph.current) {
                    cytoscape.use(svg);
                    graph.current = cytoscape({
                        elements: _elements,
                        style: style,
                        container: container.current
                    });
                }
            } catch (error) {
                console.error(error);
            }
            if (graph.current) {
                if (layout.current) {
                    layout.current.stop();
                } else {
                    redrawGraph(_elements);
                }
                graph.current.addListener('layoutstop', () => {
                    redrawGraph(_elements);
                });
            }else {
    
            }
        },
        takeImage() {
            if (graph.current) {
                cytoscape.use( svg );
                //@ts-ignore
                saveAsImage(graph.current.svg({full: true, scale: 1}))
            }
        },
        toggleGrabbable() {
            // const state = store.getState().app_reducer;
            // if (graph.current) {
            //     if(state.grabbable){
            //         graph.current.elements().grabify();
            //     } else {
            //         graph.current.elements().ungrabify();
            //     }
            // }
        }
    }));

    const ConcentricOptions = {name: 'concentric',
        concentric: function( node: any ){
            return node.data('level');
        },
        levelWidth: function( nodes: any ){
            return 1;
        },
        minNodeSpacing: 15,
    }

    const redrawGraph = (_elements: (cytoscape.NodeDefinition | cytoscape.EdgeDefinition)[]) => {
        if (graph.current) {
            graph.current.destroy();
            cytoscape.use(svg);
            graph.current = cytoscape({
                elements: _elements,
                style: style,
                container: container.current
            });
            layout.current = graph.current.elements().makeLayout(ConcentricOptions);
            layout.current.run();
        }
    }

    return (
        <div className='graph' ref={container} style={{width: '100%', height: '100%'}} />
    );
}

export default forwardRef(ReactionNetwork);
