import React, {useCallback} from 'react';
import Editor, { Monaco, useMonaco } from '@monaco-editor/react';
import { useDropzone } from 'react-dropzone'

import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { clearAppState, setChangedText, setText } from '../../app/state/appState';
import { FileType } from './recognizeType';
import { store } from '../../app/store';
import { clearState } from '../../app/state/networkState';

export function Dropzone() {
    const dispatch = useAppDispatch();
    const monaco = useMonaco();
    const text = useAppSelector(state => state.app_reducer.text);
    const currentType = FileType[useAppSelector(state => state.app_reducer.currentFileType)].toString().toLowerCase();

    // const dropzoneRef: React.RefObject<DropzoneState> = createRef();
    // const openDialog = () => {
    //   // Note that the ref is set async,
    //   // so it might be null at some point 
    //   if (dropzoneRef.current) {
    //     dropzoneRef.current.open()
    //   }
    // };

    const onDrop = useCallback((acceptedFiles) => {
      const updateText = (text: string) => {
        console.log("ClearedNetworkState");
        dispatch(clearState());
        dispatch(clearAppState());
        dispatch(setChangedText(true));
        dispatch(setText(text));
        monaco?.editor.setTheme(`org-theme-${FileType[store.getState().app_reducer.currentFileType].toLowerCase()}`);

      }
  
      acceptedFiles.forEach((file: Blob) => {
        const reader = new FileReader()
  
        reader.onabort = () => console.log('file reading was aborted')
        reader.onerror = () => console.log('file reading has failed')
        reader.onload = () => {
          const decoder = new TextDecoder("utf-8");
          const binaryStr = reader.result
          if(binaryStr instanceof ArrayBuffer) {
            updateText(decoder.decode(binaryStr));
          }
          
        }
        reader.readAsArrayBuffer(file)
      })
      
    }, [dispatch, monaco?.editor]);
  
    const {getRootProps, getInputProps} = useDropzone({onDrop, noClick: true});
    
    
    const handleEditorChange = (value: string | undefined, event: any) => {
      if (value) {
        dispatch(setChangedText(true));
        dispatch(setText(value));
        console.log(`org-theme-${FileType[store.getState().app_reducer.currentFileType].toLowerCase()}`);
        monaco?.editor.setTheme(`org-theme-${FileType[store.getState().app_reducer.currentFileType].toLowerCase()}`);
      }
    }
  
    const options = {
      minimap: {enabled: false}
    }
  
    const editorWillMount = (monaco: Monaco) => {
      monaco.languages.register({ id: 'rea' });

      monaco.languages.setMonarchTokensProvider('rea', {
        tokenizer: {
          root: [
            [/->/, "reaction"],
            [/# Components/, "components"],
            [/# Number of Components/, "component-count"],
            [/# Reactions/, "reactions"],
            [/# Number of Reactions/, "reaction-count"],
            [/[0-9.]+ /, "stoichiometry"],
            [/#.*/, "comment"],
          ]
        }
      });

      monaco.editor.defineTheme('org-theme-rea', {
        base: 'vs',
        inherit: true,
        rules: [
          { token: 'comment', foreground: '303030' },
          { token: 'components', foreground: '550000', fontStyle: 'bold' },
          { token: 'component-count', foreground: '550000', fontStyle: 'bold' },
          { token: 'reactions', foreground: '550000', fontStyle: 'bold' },
          { token: 'reaction-count', foreground: '550000', fontStyle: 'bold' },
          { token: 'reaction', foreground: 'FFA500' },
          { token: 'stoichiometry', foreground: '008800' },
        ],
        colors: {
          'editor.background': '#eee',
          'editor.lineHighlightBackground': '#eee',
          'editorLineNumber.foreground': '#008800',
        }
      });
      monaco.editor.defineTheme('org-theme-xml', {
        base: 'vs',
        inherit: true,
        rules: [],
        colors: {
          'editor.background': '#eee',
          'editor.lineHighlightBackground': '#eee',
          'editorLineNumber.foreground': '#008800',
        }
      });
    }

    return (
      <div {...getRootProps()} style={{
        height: '100%'
      }}>
        <input {...getInputProps()} />
        <Editor
          width='100%'
          height='100%'
          theme={`org-theme-${currentType}`}
          options={options}
          defaultLanguage={currentType}
          onChange={handleEditorChange}
          beforeMount={editorWillMount}
          value={text}
          key={currentType}
        />
      </div>
    )
  }
