import React, { forwardRef, useEffect, useImperativeHandle, useRef } from "react";
import cytoscape from "cytoscape";
import automove from "cytoscape-automove";
import svg from 'cytoscape-svg';
import dagre from 'cytoscape-dagre';
import { store } from "../../app/store";
import saveAsImage from "../../app/IO/saveImage";
import { CytoscapeHandle } from "../reaction_network/reactionNetwork";
import { NodeValues } from "../../app/state/appState";
import { useAppSelector } from "../../app/hooks";


type CytoscapeProps = {
    elements?: (cytoscape.NodeDefinition | cytoscape.EdgeDefinition)[];
}

const LatticeGraph: React.ForwardRefRenderFunction<CytoscapeHandle, CytoscapeProps> = (props, ref) => {
    const container = useRef<HTMLDivElement>(null);
    const graph = useRef<cytoscape.Core>();
    const layout = useRef<cytoscape.Layouts>();

    const nodeValue: NodeValues = useAppSelector(state => state.app_reducer.nodeView);

    const options: dagre.DagreLayoutOptions = {
        name: "dagre", 
        nodeSep: undefined, // the separation between adjacent nodes in the same rank
        edgeSep: undefined, // the separation between adjacent edges in the same rank
        rankSep: 100, // the separation between each rank in the layout
        rankDir: 'TB', // 'TB' for top to bottom flow, 'LR' for left to right,
        ranker: 'longest-path', // Type of algorithm to assign a rank to each node in the input graph. Possible values: 'network-simplex', 'tight-tree' or 'longest-path'
        minLen: function( edge ){ return 2; }, // number of ranks to keep between the source and target of the edge
        edgeWeight: function( edge ){ return 1; }, // higher weight edges are generally made shorter and straighter than lower weight edges

        // general layout options
        fit: true, // whether to fit to viewport
        padding: 30, // fit padding
        spacingFactor: 0.9, // Applies a multiplicative factor (>0) to expand or compress the overall area that the nodes take up
        nodeDimensionsIncludeLabels: true, // whether labels should be included in determining the space used by a node
        animate: false, // whether to transition the node positions
        animateFilter: function( node, i ){ return true; }, // whether to animate specific nodes when animation is on; non-animated nodes immediately go to their final positions
        animationDuration: 500, // duration of animation in ms if enabled
        animationEasing: undefined, // easing of animation if enabled
        boundingBox: undefined, // constrain layout bounds; { x1, y1, x2, y2 } or { x1, y1, w, h }
        transform: function( node, pos ){ const num = Number.parseInt(node.data('rank')); return {x: pos.x, y: num * -80}; }, // a function that applies a transform to the final node position
        ready: function(){}, // on layoutready
        stop: function(){},
    }

    const nodeStyle: cytoscape.Stylesheet[] = [
        {
            selector: 'node',
            style: {
                'shape': 'rectangle',
                'font-size': 12,
                'background-color': '#fff',
                'label': function(ele: cytoscape.NodeSingular){
                    let dataStr = 'label';
                    if(nodeValue===NodeValues.ALLSPECIES) {
                        dataStr = 'all_species';
                    } else if (nodeValue===NodeValues.NOVELTY) {
                        dataStr = 'novelty_species';
                    }
                    return ele.data(dataStr);
                },
                'width': function(ele: cytoscape.NodeSingular){
                    let dataStr = 'normalWidth';
                    if(nodeValue===NodeValues.ALLSPECIES) {
                        dataStr = 'all_species_width';
                    } else if (nodeValue===NodeValues.NOVELTY) {
                        dataStr = 'novelty_species_width';
                    }
                    return ele.data(dataStr);
                },
                'height': '30px',
                'color': 'data(color)',
                'text-valign': 'center',
                'text-halign': 'center',
                'border-color': '#000',
                'border-width': 2,
                'border-style': 'solid',
            }
        },
    ];
    const edgeStyle: cytoscape.Stylesheet[] = [{
        selector: 'edge',
        style: {
            'width': 3,
            'line-color': '#ccc',
            'control-point-step-size': 50,
            'source-endpoint': 'inside-to-node',
        }
    }];

    const style: cytoscape.Stylesheet[] = [...nodeStyle, ...edgeStyle];

    let defaults = {
        nodesMatching: function( node: cytoscape.NodeSingular ){ return true; },
        reposition: function( node: cytoscape.NodeSingular ){ const num = Number.parseInt(node.data('rank')); return {x: node.position('x'), y: num * -80}; },
        when: 'matching',
        meanIgnores: function( node: cytoscape.NodeSingular ){ return false; },
        meanOnSelfPosition: function( node: cytoscape.NodeSingular ){ return true; },
        dragWith: function( node: cytoscape.NodeSingular ){ return false; }
      };
       
      let _options = defaults;
       
      

    useEffect(() => {
        const _elements = store.getState().app_reducer.latticeGraph;
        if(_elements !== []) {
            try {
                if (!graph.current) {
                    cytoscape.use(dagre);
                    cytoscape.use(automove);
                    cytoscape.use( svg );
                    graph.current = cytoscape({
                        elements: _elements,
                        style: style,
                        container: container.current
                    });
                    //@ts-ignore
                    let rule = graph.current.automove(_options);
                    rule.apply();
                }
            } catch (error) {
                console.error(error);
            }
            if (graph.current) {
                if (layout.current) {
                    layout.current.stop();
                } else {
                    redrawGraph(_elements);
                }
                graph.current.addListener('layoutstop', () => {
                    redrawGraph(_elements);
                });
            }else {

            }
        }
        
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useImperativeHandle(ref, ()=> ({
        draw() {
            const _elements = store.getState().app_reducer.latticeGraph;
            try {
                if (!graph.current) {
                    cytoscape.use(dagre);
                    cytoscape.use(automove);
                    cytoscape.use( svg );
                    graph.current = cytoscape({
                        elements: _elements,
                        style: style,
                        container: container.current
                    });
                    //@ts-ignore
                    let rule = graph.current.automove(_options);
                    rule.apply();
                }
            } catch (error) {
                console.error(error);
            }
            if (graph.current) {
                if (layout.current) {
                    layout.current.stop();
                } else {
                    redrawGraph(_elements);
                }
                graph.current.addListener('layoutstop', () => {
                    redrawGraph(_elements);
                });
            }else {
    
            }
        },
        takeImage() {
            if (graph.current) {
                //@ts-ignore
                const svg_content = graph.current.svg({full: true, scale: 1});
                saveAsImage(svg_content);
            }
        },
        toggleGrabbable() {
            // const state = store.getState().app_reducer;
            // if (graph.current) {
            //     if(state.grabbable){
            //         graph.current.elements().grabify();
            //     } else {
            //         graph.current.elements().ungrabify();
            //     }
            // }
        }
    }));

    const redrawGraph = (_elements: (cytoscape.NodeDefinition | cytoscape.EdgeDefinition)[]) => {
        if (graph.current) {
            graph.current.destroy();
            graph.current = cytoscape({
                elements: _elements,
                style: style,
                container: container.current
            });
            //@ts-ignore
            let rule = graph.current.automove(_options);
            rule.apply();
            // layout.current = graph.current.elements().makeLayout({name: 'preset'});
            layout.current = graph.current.elements().makeLayout(options);
            layout.current.run();
        }
    }

    return (
        <div className='graph' ref={container} style={{width: '100%', height: '100%'}} />
    );
}

export default forwardRef(LatticeGraph);
