import * as Comlink from 'comlink';
import { EdgeDefinition } from 'cytoscape';
import FileSaver from 'file-saver';
import { store } from '../../app/store';
import { setRunning } from '../state/appState';
import latticeAPI, { LatticeWorker } from './lattice.worker';

export default function generateLatticeFile() {
    store.dispatch(setRunning(1));

    const state = store.getState();
    const organizations = state.network_reducer.organizations;
    const edges = state.app_reducer.latticeGraph.slice(organizations.length) as EdgeDefinition[];
    const worker: Comlink.Remote<LatticeWorker> = Comlink.wrap(new latticeAPI());
    worker.generateLatticeString(organizations, edges, Comlink.proxy(callback));
}

function callback(lattice: string): void {
    let blob = new Blob([lattice], {
        type: "text/plain;charset=utf-8"
    });
    FileSaver.saveAs( blob, 'lattice.ltc' );
    store.dispatch(setRunning(-1));
}
