import * as Comlink from 'comlink';
import { GLPK } from "glpk.js";
import { Organization, Reaction, Species } from '../../app/';
import { setOrgList, setRunning } from '../../app/state/appState';
import { setOrganizations, setSemiOrganizations } from '../../app/state/networkState';
import { store } from '../../app/store';
import { generateOrganizations } from './computeOrgs';
import organizationAPI, { OrganizationWorker } from './organization.worker';

export default function calculateOrganizations(connected: boolean) {

    store.dispatch(setRunning(1));

    const state = store.getState();
    const verbose = state.app_reducer.verbose;
    // if(verbose) {
        console.time('calculateOrganizations');
    // }
    const species = Object.values(state.network_reducer.species);
    const reactionList = Object.values(state.network_reducer.reactions);
    const worker: Comlink.Remote<OrganizationWorker> = Comlink.wrap(new organizationAPI());
    
    worker.calculateOrganizations(connected, verbose, species, reactionList, Comlink.proxy(callback));
}

function callback(semiOrganizations: number[], verbose: boolean, error: any | undefined): void {
    if(error) {
        alert(error + '\n\nCheck the console for more information.');
    }
    require('glpk.js').then((glpk: GLPK) => {
        const state = store.getState();
        const species = Object.values(state.network_reducer.species);
        const reactions = Object.values(state.network_reducer.reactions);
        const numberOfSpecies = species.length;
        let _semiOrganizations: Organization[] = [];
        let organizations: Organization[] = [];
        
        
            
        for(let i = 0; i < semiOrganizations.length; i += numberOfSpecies+1) {
            const semiOrg = semiOrganizations.slice(i, i+numberOfSpecies+1);
            const index = organizations.length+1;
            _semiOrganizations.push({
                id: `SO${i/(numberOfSpecies+1)}`,
                species: setToSpeciesArray(semiOrg, species),
                reactions: reactionsOfInterest(semiOrg, reactions, species),
                stoichiometry: generateStochiometriy(semiOrg, reactions, species)
            });
            const organization = generateOrganizations(semiOrg, index, species, reactions, glpk);
            
            if(organization) {
                if(verbose) {
                    console.log(organization as Organization)
                }
                organizations.push(organization as Organization);
            }
        }
        
        store.dispatch(setSemiOrganizations(_semiOrganizations));
        store.dispatch(setOrganizations(organizations));
        store.dispatch(setOrgList(organizations.length));
        store.dispatch(setRunning(-1));
        if(verbose) {
            console.log(store.getState());
        }

        // if(verbose) {
        console.timeEnd('calculateOrganizations')
        // }
    });
}

function setToSpeciesArray(set: number[], speciesList: Species[]) {
    const result: Species[] = [];
    
    for (let index = 1; index < set.length; index++) {
        if(set[index] !== 0) {
            result.push(speciesList[index-1]);
        }
        
    }
    
    return result;
}

function reactionsOfInterest(set: number[], reactionList: Reaction[], speciesList: Species[]): Reaction[] {
    const reactions: Reaction[] = [];
    for(const reaction of reactionList) {
        const educts = reaction.stoichiometry.slice(0,speciesList.length);
        if(contains(set.slice(1), educts)) {
            reactions.push(reaction);
        }
    }

    return reactions;
}

function contains(largeSet: number[], smallSet: number[]): boolean {
    for(let i = 0; i < smallSet.length; i++) {
        if(smallSet[i] > 0) {
            if(largeSet[i] !== 1) {
                return false;
            }
        }
    }
    return true;
}

function generateStochiometriy(semiOrg: number[], reactionList: Reaction[], speciesList: Species[]): number[][] {
    let  matrix: number[][] = [];
    for (const reaction of reactionList) {
        const reactants = reaction.stoichiometry.slice(0,speciesList.length);
        const products = reaction.stoichiometry.slice(speciesList.length, speciesList.length*2);

        const row = [];
        for (let i = 0; i < reactants.length; i++) {
            row.push(products[i]-reactants[i]);
        }
        matrix.push(row);
    }
    matrix = transpose(matrix);
    return matrix;
}

function transpose(matrix: number[][]) {
    const rows = matrix.length, cols = matrix[0]?.length | 0;
    const grid = [];
    for (let j = 0; j < cols; j++) {
      grid[j] = Array(rows);
    }
    for (let i = 0; i < rows; i++) {
      for (let j = 0; j < cols; j++) {
        grid[j][i] = matrix[i][j];
      }
    }
    return grid;
  }
