import * as Comlink from 'comlink';
import { EdgeDefinition, NodeDefinition } from 'cytoscape';
import { Species } from '../../app/';
import { setLatticeGraph, setRunning } from '../../app/state/appState';
import { store } from '../../app/store';
import graphAPI, { GraphWorker } from './graph.worker';

export default function generateLattice(nodes?: NodeDefinition[], edges?: EdgeDefinition[]) {
    store.dispatch(setRunning(1));
    const state = store.getState().network_reducer;
    const speciesList = Object.values(state.species);
    const organizations = state.organizations.map(organization => speciesArrayToIndexArray(organization.species, speciesList));
    const worker: Comlink.Remote<GraphWorker> = Comlink.wrap(new graphAPI());
    if(nodes && edges) {
        worker.setGraph(nodes, edges);
    }
    worker.generateGraph(organizations, speciesList, Comlink.proxy(callback));
}

function callback(nodes: NodeDefinition[], edges: EdgeDefinition[], error: any | undefined): void {
    if(error) {
        alert(error + '\n\nCheck the console for more information.');
    } else {
        store.dispatch(setLatticeGraph([...nodes, ...edges]));
        store.dispatch(setRunning(-1));
    }
}


function speciesArrayToIndexArray(species: Species[], speciesList: Species[]): number[] {
    const arr: number[] = [];
    for(const element of species) {
        arr.push(speciesToIndex(element, speciesList));
    }
    return arr;
}

function speciesToIndex(species: Species, speciesList: Species[]): number {
    return speciesList.findIndex((element) => element.uid === species.uid);
}
