import REAReader from "../../app/IO/rea";
import SBMLReader from "../../app/IO/sbml";
import { setChangedText } from "../../app/state/appState";
import { setReaction, setSpecies } from "../../app/state/networkState";
import { store } from "../../app/store";
import { FileType } from "./recognizeType";

export default function readFile() {
    const state = store.getState().app_reducer;
    const text = state.text;
    const filetype = state.currentFileType;

    const { species, reactions } = filetype === FileType.REA ? REAReader(text) : SBMLReader(text);
    
    store.dispatch(setChangedText(false));
    store.dispatch(setSpecies(species));
    store.dispatch(setReaction(reactions));
}
