import * as Comlink from 'comlink';
import { EdgeDefinition } from 'cytoscape';
import FileSaver from 'file-saver';
import { store } from '../../app/store';
import { setRunning } from '../state/appState';
import orgmlAPI, { OrgMLWorker } from './orgml.worker';

export default function generateOrgML() {
    store.dispatch(setRunning(1));

    const state = store.getState();
    const organizations = state.network_reducer.organizations;
    const edges = state.app_reducer.latticeGraph.slice(organizations.length) as EdgeDefinition[];
    const worker: Comlink.Remote<OrgMLWorker> = Comlink.wrap(new orgmlAPI());
    worker.generateOrgMLString(organizations, edges, Comlink.proxy(callback));
}

function callback(orgml: string): void {
    let blob = new Blob([orgml], {
        type: "text/plain;charset=utf-8"
    });
    FileSaver.saveAs( blob, 'orgml_org.xml' );
    store.dispatch(setRunning(-1));
}
