import FileSaver from "file-saver";
import { Reaction } from "../";
import { Species } from "../";
import MultiSet from "../classes/multiSet";

export default function REAReader (rea: string): {
    species: {
        [key: string]: Species;
    };
    reactions: {
        [key: string]: Reaction;
    };
} {
    const speciesObject: { [key: string] : Species} = {};
    const reactionObject: { [key: string] : Reaction} = {};

    const lines: string[] = rea.split('\n');

    // console.log(lines);

    let numberOfSpecies = 0;
    let numberOfReactions = 0;
    let speciesCount = 0;
    let reactionCount = 0;
    let lastHashtag = '';

    for (let i = 0; i < lines.length; i++) {
        const line = lines[i].trim();
        if (line[0] === '#') {
            lastHashtag = line;
        }
        if(line.includes('# Number of Components')) {
            numberOfSpecies = Number.parseInt(lines[i+1]);
        } else if (line.includes('# Number of Reactions')) {
            // eslint-disable-next-line @typescript-eslint/no-unused-vars
            numberOfReactions = Number.parseInt(lines[i+1]);
        } else {
            while(lastHashtag.includes('# Components') && !line.includes('# Components')) {
                const _line = lines[i];

                if (_line[0] === '#') {
                    lastHashtag = _line;
                    break;
                }

                if(_line === undefined) {
                    break;
                } else if(_line.length === 0) {
                    i++;
                    continue;
                }

                const species: Species = {
                    uid: _line,
                    id: _line,
                    index: speciesCount++,
                    meta: {
                    }
                }
                speciesObject[`${species.uid}`] = species;

                i++;
            }
            while(lastHashtag.includes('# Reactions') && !line.includes('# Reactions')) {
                const _line = lines[i];
                if(_line === undefined) {
                    break;
                } else if(_line.length === 0) {
                    i++;
                    continue;
                }

                const speciesArray = Object.keys(speciesObject);

                const reactantArray: Species[] = [];
                const productArray: Species[] = [];
                const stoichiometry: number[] = new Array(numberOfSpecies*2).fill(0);

                const [reactants, products] = _line.split('->').map(_ => _.trim().split(' '));

                for (let r = 0; r < reactants.length; r++) {
                    const element = reactants[r];

                    if(element.length === 0) {
                        continue;
                    }

                    if(r%2 === 0) {
                        // Coefficient of Species
                        const index = speciesArray.findIndex(element => element === reactants[r+1]);
                        stoichiometry[index] = Number.parseFloat(element);
                    } else {
                        // Species
                        reactantArray.push(speciesObject[`${element}`]);
                    }
                }

                for (let p = 0; p < products.length; p++) {
                    const element = products[p];

                    if(element.length === 0) {
                        continue;
                    }

                    if(p%2 === 0) {
                        // Coefficient of Species
                        const index = speciesArray.findIndex(element => element === products[p+1]);
                        stoichiometry[numberOfSpecies+index] = Number.parseFloat(element);
                    } else {
                        // Species
                        productArray.push(speciesObject[`${element}`]);
                    }
                }
    
                const reaction: Reaction = {
                    index: reactionCount++,
                    uid: `${speciesarr2str(reactantArray, speciesArray, stoichiometry, 0)} -> ${speciesarr2str(productArray, speciesArray, stoichiometry, numberOfSpecies)}`,
                    reversable: false,
                    reactants: new MultiSet(reactantArray, numberOfSpecies),
                    products: new MultiSet(productArray, numberOfSpecies),
                    stoichiometry: stoichiometry,
                }
                reactionObject[`${reaction.uid}`] = reaction;

                if (_line[0] === '#') {
                    lastHashtag = _line;
                    break;
                }
                i++;
            }
        }
    }

    return {species: speciesObject, reactions: reactionObject};
}

const speciesarr2str = (arr: Species[], speciesArray: string[], stoichiometry: number[], offset: number) => {
    let str = '';

    arr.forEach(element => {
        let index = speciesArray.findIndex(ele => ele === element.uid);
        str = `${str} ${element.uid}[${stoichiometry[index + offset]}]`;
    });

    return str.trimStart();
}

export function REAWriter (rea: string) {
    let blob = new Blob([rea], {
        type: "text/plain;charset=utf-8"
    });
    FileSaver.saveAs( blob, 'Network.rea' );
}
