import { ExpandLess, ExpandMore } from '@mui/icons-material';
import { Collapse, List, ListItem, ListItemText, ListSubheader, Typography } from '@mui/material';
import React from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { toggleOrgList } from '../../app/state/appState';

export default function OrganizationList() {
    const dispatch = useAppDispatch();
    const organizations = useAppSelector(state => state.network_reducer.organizations);
    const hooks = useAppSelector(state => state.app_reducer.orgList);

    const primary = '#002350';
    const secondary = '#33393961';

    return (
        <List>
            <ListSubheader sx={{color: primary, backgroundColor: '#eee'}}>Found {organizations.length} Organizations</ListSubheader>
            {organizations.map(organization => {
                const id = Number.parseInt(organization.id.slice(1))-1;
                return (
                    <div key={id}>
                        <ListItem button onClick={()=>{dispatch(toggleOrgList(id))}}>
                            <ListItemText primary={`Organization: ${organization.id}`} sx={{ color: primary}}/>
                            {hooks[id] ? <ExpandLess sx={{color: primary}}/> : <ExpandMore sx={{color: secondary}}/>}
                        </ListItem>
                        <Collapse in={hooks[id]} timeout="auto" unmountOnExit>
                            <List component="div" sx={{marginLeft: '50px'}}>
                                <ListItemText primary="Species:" sx={{ color: primary}}/>
                                <Typography sx={{marginLeft: '20px', wordWrap: 'normal'}}>{organization.species.length === 0 ? '{}' : organization.species.map(s => {
                                    return `${s.id}, `
                                })}</Typography>
                                <ListItemText primary="Reactions:" sx={{ color: primary}}/>
                                {organization.reactions.map(r => {
                                    return (<Typography key={r.uid} sx={{marginLeft: '20px', wordWrap: 'normal'}}>{r.uid}</Typography>);
                                })}
                            </List>
                        </Collapse>
                    </div>
                );
            })}
        </List>
    );
}
