import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { EdgeDefinition, NodeDefinition } from "cytoscape";
import recoginizeType, { FileType } from "../../features/editor/recognizeType";
import { RootState } from "../store";

export enum NodeValues {
    ORGNAMES,
    NOVELTY,
    ALLSPECIES
}

interface AppState {
    text: string,
    changedText: boolean,
    core: number,
    verbose: boolean,
    running: number,
    currentWindow: string,
    currentFileType: FileType,
    hash: Map<string, number>[],
    networkGraph: (NodeDefinition | EdgeDefinition)[],
    latticeGraph: (NodeDefinition | EdgeDefinition)[],
    orgList: boolean[],
    nodeView: NodeValues,
}

const initialState: AppState = {
    text: `# Minimal example from Peter Dittrich and Pietro Speroni di Fenizio
# From 'Chemical Organisation Theory', Bulletin of Mathematical Biology (2007) 69: 1199â€“1231 
# [accessed Sep. 24 2021]
# Number of Components
4
# Components
A
B
C
D
# Number of Reactions
8
# Reactions
1 A 1 B -> 1 A 2 B
1 A 1 D -> 1 A 2 D
1 B -> 
1 B 1 C -> 2 C
1 B 1 D -> 1 C
1 C -> 
1 C -> 1 B
1 D -> 
`,
    changedText: true,
    core: 1,
    verbose: false,
    running: 0,
    currentWindow: 'Editor',
    currentFileType: FileType.REA,
    hash: [],
    networkGraph: [],
    latticeGraph: [],
    orgList: [],
    nodeView: NodeValues.ALLSPECIES,
};

const appState = createSlice({
    name: 'app',
    initialState,
    reducers: {
        setText: (state, action: PayloadAction<string>) => {
            state.text = action.payload;
            state.currentFileType = recoginizeType(action.payload.split('\n')[0]);
        },
        setChangedText: (state, action: PayloadAction<boolean>) => {
            state.changedText = action.payload;
        },
        toggleVerbose: (state, action: PayloadAction<boolean>) => {
            state.verbose = action.payload;
        },
        setRunning: (state, action: PayloadAction<number>) => {
            state.running += action.payload;
        },
        setCurrentWindow: (state, action: PayloadAction<string>) => {
            state.currentWindow = action.payload;
        },
        setNetworkGraph: (state, action: PayloadAction<(NodeDefinition | EdgeDefinition)[]>) => {
            state.networkGraph = action.payload;
        },
        setLatticeGraph: (state, action: PayloadAction<(NodeDefinition | EdgeDefinition)[]>) => {
            state.latticeGraph = action.payload;
        },
        setOrgList: (state, action: PayloadAction<number>) => {
            state.orgList = new Array(action.payload).fill(false);
        },
        toggleOrgList: (state, action: PayloadAction<number>) => {
            state.orgList[action.payload] = !state.orgList[action.payload];
        },
        setNodeView: (state, action: PayloadAction<NodeValues>) => {
            state.nodeView = action.payload;
        },
        clearAppState: (state) => {
            console.log("ClearedAppState");
            state.text = '';
            state.hash = [];
            state.networkGraph = [];
            state.latticeGraph = [];
            state.orgList = [];
        },
    }
});

const appReducer = appState.reducer;

export const { 
    setText,
    setChangedText,
    toggleVerbose,
    setRunning,
    setCurrentWindow,
    setNetworkGraph,
    setLatticeGraph, 
    setOrgList,
    toggleOrgList,
    setNodeView,
    clearAppState
} = appState.actions;

export const selectText = (state: RootState) => state.app_reducer.text;
export const selectCores = (state: RootState) => state.app_reducer.core;
// export const selectOrganizationGraph = (state: RootState) => state.app_reducer.orgGraph;
// export const selectNetworkGraph = (state: RootState) => state.app_reducer.networkGraph;

export { appState, appReducer };

export type { AppState };
