import React from 'react';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import TextFieldsIcon from '@mui/icons-material/TextFields';
import TrendingFlatIcon from '@mui/icons-material/TrendingFlat';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import ScatterPlotIcon from '@mui/icons-material/ScatterPlot';
import DownloadIcon from '@mui/icons-material/Download';
import CachedIcon from '@mui/icons-material/Cached';
import SearchIcon from '@mui/icons-material/Search';
import CreateIcon from '@mui/icons-material/Create';
import ImageIcon from '@mui/icons-material/Image';
import OpenInBrowserIcon from '@mui/icons-material/OpenInBrowser';
import { clearAppState, setChangedText, setCurrentWindow, setText } from '../app/state/appState';
import { useAppDispatch, useAppSelector } from '../app/hooks';
import { Collapse, List } from '@mui/material';
import readFile from '../features/editor/readFile';
import generateReactionNetwork from '../features/reaction_network/generateNetwork';
import calculateOrganizations from '../features/organizations/calculateOrganizations';
import generateLattice from '../features/lattice/generateGraph';
import generateOrgML from '../app/IO/orgmlExport';
import generateLatticeFile from '../app/IO/latticeExport';
import writeFile from '../features/editor/writeFile';
import { LabelImportant } from '@mui/icons-material';
import fileDialog from 'file-dialog';
import { clearState } from '../app/state/networkState';

const primary = '#002350';
const secondary = '#33393961';

interface ListItemProps {
  saveNetworkImage: () => void;
  saveLatticeImage: () => void;
}

export function MainListItems(props: ListItemProps) {
  const dispatch = useAppDispatch();

  const title = useAppSelector(state => state.app_reducer.currentWindow);
  const orgCount = useAppSelector(state => state.network_reducer.organizations.length);
  const latticeDrawn = useAppSelector(state => state.app_reducer.latticeGraph.length);
  const networkDrawn = useAppSelector(state => state.app_reducer.networkGraph.length);
  const changed = useAppSelector(state => state.app_reducer.changedText);

  const [openE, setOpenE] = React.useState(true);
  const [openRN, setOpenRN] = React.useState(false);
  const [openLtc, setOpenLtc] = React.useState(false);
  const [openOrg, setOpenOrg] = React.useState(false);

  const handleClick = (value: string) => {
    dispatch(setCurrentWindow(value));
    if(value === 'Editor') {
      setOpenE(true);
      setOpenRN(false);
      setOpenLtc(false);
      setOpenOrg(false);
    } else if(value === 'Reaction Network') {
      setOpenE(false);
      setOpenRN(true);
      setOpenLtc(false);
      setOpenOrg(false);
    } else if (value === 'Lattice') {
      setOpenE(false);
      setOpenRN(false);
      setOpenLtc(true);
      setOpenOrg(false);
    } else {
      setOpenE(false);
      setOpenRN(false);
      setOpenLtc(false);
      setOpenOrg(true);
    }
  }

  const openFile = () => {
    fileDialog({multiple: false, accept: ['.rea', 'text/xml']}, file => {
      file[0].text().then(result => {
        dispatch(clearState());
        dispatch(clearAppState());
        dispatch(setChangedText(true));
        dispatch(setText(result));
      });
    });
  }

  const listLabel = (current: string) => {
    if(title === current) {
      return <ListItemText primary={current} sx={{ color: primary}}/>
    } else {
      return <ListItemText primary={current} sx={{ color: secondary}}/>
    }
  };
  const listIcon = (
    current: string,
    icon:JSX.Element
    ) => {
      if(title === current) {
        return ( <ListItemIcon sx={{ color: primary}}>{icon}</ListItemIcon> );
      } else {
        return ( <ListItemIcon sx={{ color: secondary}}>{icon}</ListItemIcon> );
      }
  };

  return (
    <List>
      <ListItem button onClick={()=>{handleClick('Editor')}}>
        {listIcon('Editor', <TextFieldsIcon />)}
        {listLabel('Editor')}
        {openE ? <ExpandLess sx={{color: primary}}/> : <ExpandMore sx={{color: secondary}}/>}
      </ListItem>
      <Collapse in={openE} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button sx={{ pl: 4 }} onClick={() => openFile()}>
            <ListItemIcon sx={{ color: primary}}>
              <OpenInBrowserIcon />
            </ListItemIcon>
            <ListItemText primary="Open File" sx={{ color: primary}}/>
          </ListItem>
          <ListItem button sx={{ pl: 4 }} onClick={() => writeFile()}>
            <ListItemIcon sx={{ color: primary}}>
              <DownloadIcon />
            </ListItemIcon>
            <ListItemText primary="Save File" sx={{ color: primary}}/>
          </ListItem>
          <ListItem disabled button sx={{ pl: 4 }} onClick={() => writeFile()}>
            <ListItemIcon sx={{ color: primary}}>
              <CachedIcon />
            </ListItemIcon>
            <ListItemText primary="Convert File" sx={{ color: primary}}/>
          </ListItem>
          <ListItem button sx={{ pl: 4 }} onClick={() => readFile()}>
            <ListItemIcon sx={{ color: changed ? '#BC4A09' : primary}}>
              <LabelImportant />
            </ListItemIcon>
            <ListItemText primary="Read" sx={{ color: changed ? '#BC4A09' : primary}}/>
          </ListItem>
        </List>
      </Collapse>
      <ListItem button onClick={()=>{handleClick('Reaction Network');}}>
        {listIcon('Reaction Network', <TrendingFlatIcon />)}
        {listLabel('Reaction Network')}
        {openRN ? <ExpandLess sx={{color: primary}}/> : <ExpandMore sx={{color: secondary}}/>}
      </ListItem>
      <Collapse in={openRN} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button sx={{ pl: 4 }} onClick={() => {generateReactionNetwork();}}>
            <ListItemIcon sx={{ color: primary}}>
              <CreateIcon />
            </ListItemIcon>
            <ListItemText primary="Draw Network Graph" sx={{ color: primary}}/>
          </ListItem>
          <ListItem disabled={networkDrawn > 0 ? false : true} button sx={{ pl: 4 }} onClick={() => props.saveNetworkImage()}>
            <ListItemIcon sx={{ color: primary}}>
              <ImageIcon />
            </ListItemIcon>
            <ListItemText primary="Save Image" sx={{ color: primary}}/>
          </ListItem>
        </List>
      </Collapse>
      <ListItem button onClick={()=>{handleClick('Lattice')}}>
        {listIcon('Lattice', <AccountTreeIcon style={{ transform: 'rotate(90deg)' }} />)}
        {listLabel('Lattice')}
        {openLtc ? <ExpandLess sx={{color: primary}}/> : <ExpandMore sx={{color: secondary}}/>}
      </ListItem>
      <Collapse in={openLtc} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem disabled={orgCount > 0 ? false : true} button sx={{ pl: 4 }} onClick={() => {generateLattice();}}>
            <ListItemIcon sx={{ color: primary}}>
              <CreateIcon />
            </ListItemIcon>
            <ListItemText primary="Draw Lattice" sx={{ color: primary}}/>
          </ListItem>
          <ListItem disabled={latticeDrawn > 0 ? false : true} button sx={{ pl: 4 }} onClick={() => {generateLatticeFile()}}>
            <ListItemIcon sx={{ color: primary}}>
              <DownloadIcon />
            </ListItemIcon>
            <ListItemText primary="Save Lattice" sx={{ color: primary}}/>
          </ListItem>
          <ListItem disabled={latticeDrawn > 0 ? false : true} button sx={{ pl: 4 }} onClick={() => props.saveLatticeImage()}>
            <ListItemIcon sx={{ color: primary}}>
              <ImageIcon />
            </ListItemIcon>
            <ListItemText primary="Save Image" sx={{ color: primary}}/>
          </ListItem>
        </List>
      </Collapse>
      <ListItem button onClick={()=>{handleClick('Organizations')}}>
        {listIcon('Organizations', <ScatterPlotIcon />)}
        {listLabel('Organizations')}
        {openOrg ? <ExpandLess sx={{color: primary}}/> : <ExpandMore sx={{color: secondary}}/>}
      </ListItem>
      <Collapse in={openOrg} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button sx={{ pl: 4 }} onClick={() => {calculateOrganizations(false)}}>
            <ListItemIcon sx={{ color: primary}}>
              <SearchIcon />
            </ListItemIcon>
            <ListItemText primary="Find Organizations" sx={{ color: primary}}/>
          </ListItem>
          <ListItem button sx={{ pl: 4 }} onClick={() => {calculateOrganizations(true)}}>
            <ListItemIcon sx={{ color: primary}}>
              <SearchIcon />
            </ListItemIcon>
            <ListItemText primary="Find Connected Orgs" sx={{ color: primary}}/>
          </ListItem>
          <ListItem disabled={orgCount > 0 && latticeDrawn > 0 ? false : true} button sx={{ pl: 4 }} onClick={() => {generateOrgML()}}>
            <ListItemIcon sx={{ color: primary}}>
              <DownloadIcon />
            </ListItemIcon>
            <ListItemText primary="Save Organizations" sx={{ color: primary}}/>
          </ListItem>
        </List>
      </Collapse>
    </List>
  );
}
