import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import{ Species, Reaction, Organization } from '../';
import { RootState } from "../store";

/**
 * The current network state.
 * Contains an array of species and an array of reactions.
 */
 interface NetworkState {
    /**
     * Dictionary of species present in network. 
     * Key is the uid of the species.
    */
    species: { [key: string]: Species },
    /**
     * Dictionary of the interactions between species in the network. 
     * The key is a reaction string like A->B.
    */
    reactions: { [key: string]: Reaction },
    SOsToCheck: number[],
    semiOrganisations: Organization[],
    organizations: Organization[],
}

const initialState: NetworkState = {
    species: {},
    reactions: {},
    SOsToCheck: [],
    semiOrganisations: [],
    organizations: [],
}
const networkState = createSlice({
    name: 'network',
    initialState,
    reducers: {
        setSpecies: (state, action: PayloadAction<{[key: string]: Species;}>) => {
            
            state.species = action.payload;
        },
        setReaction: (state, action: PayloadAction<{[key: string]: Reaction;}>) => {

            state.reactions = action.payload;
        },
        addSOtoCheck: (state, action: PayloadAction<number[]>) => {
            const toAdd: number[] = action.payload;
            const arr = Array.from(state.SOsToCheck);
            arr.push(...toAdd);
            state.SOsToCheck = arr;
        },
        removeAtIndex: (state, action: PayloadAction<[number, number]>) => {
            const arr = Array.from(state.SOsToCheck);
            const count = action.payload[0] + action.payload[1] + 1;
            arr.splice(action.payload[0], count);
            state.SOsToCheck = arr;
        },
        setSemiOrganizations: (state, action: PayloadAction<Organization[]>) => {
            state.semiOrganisations = action.payload;
        },
        setOrganizations: (state, action: PayloadAction<Organization[]>) => {
            state.organizations = action.payload;
        },
        clearState: state => {
            state.reactions = {};
            state.species = {};
            state.SOsToCheck = [];
            state.semiOrganisations = [];
            state.organizations = [];
        }
    }
});

const networkReducer = networkState.reducer;

export const { setSpecies, setReaction, addSOtoCheck, setSemiOrganizations, setOrganizations, removeAtIndex, clearState } = networkState.actions;

export const selectSpecies = (state: RootState) => state.network_reducer.species;
export const selectReactions = (state: RootState) => state.network_reducer.reactions;

export { networkState, networkReducer };

export type { NetworkState };
