import { AppBar } from './components/Appbar';
import React, { ElementRef, useRef } from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {
    Box,
    Button,
    Checkbox,
    CircularProgress,
    CssBaseline, 
    Dialog, 
    DialogActions, 
    DialogContent, 
    DialogTitle, 
    Divider,
    IconButton,
    Paper,
    styled,
    Toolbar,
    Typography
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import SettingsIcon from '@mui/icons-material/Settings';
import MuiDrawer from '@mui/material/Drawer';
import { MainListItems } from './components/listItems';
import { Dropzone } from './features/editor/dropzone';
import { useAppDispatch, useAppSelector } from './app/hooks';
import ReactionNetwork, { CytoscapeHandle } from './features/reaction_network/reactionNetwork';
import { store } from './app/store';
import { EdgeDefinition, NodeDefinition } from 'cytoscape';
import OrganizationList from './features/organizations/organizationList';
import LatticeGraph from './features/lattice/latticeGraph';
import { toggleVerbose } from './app/state/appState';
import { NodeViewDropdown } from './components/nodeViewDropdown';

const drawerWidth: number = 280;

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
        '& .MuiDrawer-paper': {
                position: 'relative',
                whiteSpace: 'nowrap',
                width: drawerWidth,
                transition: theme.transitions.create('width', {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.enteringScreen,
            }),
            boxSizing: 'border-box',
            ...(!open && {
                overflowX: 'hidden',
                transition: theme.transitions.create('width', {
                    easing: theme.transitions.easing.sharp,
                    duration: theme.transitions.duration.leavingScreen,
                }),
                width: theme.spacing(7),
                [theme.breakpoints.up('sm')]: {
                    width: theme.spacing(9),
                },
            }),
        },
    }),
);

interface TabPanelProps {
    children?: React.ReactNode;
    index: string;
    value: string;
}

function TabPanel(props: TabPanelProps) {
    const { children, value, index, ...other } = props;

    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        style={{height: '100%'}}
        {...other}
      >
        {value === index && (
          children
        )}
      </div>
    );
}

const mdTheme = createTheme();
type GraphHandle = ElementRef<typeof ReactionNetwork>
let networkRef: React.RefObject<CytoscapeHandle>;
type LatticeHandle = ElementRef<typeof LatticeGraph>
let latticeRef: React.RefObject<CytoscapeHandle>;

export default function Main() {
    const [open, setOpen] = React.useState(false);
    const [settingsOpen, setSettingsOpen] = React.useState(false);
    const title = useAppSelector(state => state.app_reducer.currentWindow);
    const running = useAppSelector(state => state.app_reducer.running);
    const verbose = useAppSelector(state => state.app_reducer.verbose);

    const dispatch = useAppDispatch();
    
    networkRef = useRef<GraphHandle>(null);
    latticeRef = useRef<LatticeHandle>(null);

    const toggleDrawer = () => {
        setOpen(!open);
    };

    const handleClickOpen = () => {
        setSettingsOpen(true);
    };

    const handleClose = (event: React.SyntheticEvent<unknown>, reason?: string) => {
        if (reason !== 'backdropClick') {
            setSettingsOpen(false);
        }
    };

    const handleSaveImage = () => {
        if(networkRef.current) {
            networkRef.current.takeImage();
        }
    }

    const handleSaveLatticeImage = () => {
        if(latticeRef.current) {
            latticeRef.current.takeImage();
        }
    }

    let oldNetwork: (NodeDefinition | EdgeDefinition)[] = [];
    let oldlattice: (NodeDefinition | EdgeDefinition)[] = [];
    store.subscribe(() => {
        const state = store.getState();
        const currentNetwork = state.app_reducer.networkGraph;
        const currentLattice = state.app_reducer.latticeGraph;
        if(oldNetwork !== currentNetwork) {
            networkRef.current?.draw();
            oldNetwork = currentNetwork;
        }
        if(oldlattice !== currentLattice) {
            if(latticeRef.current) {
                latticeRef.current.draw();
                oldlattice = currentLattice;
            }
            
        }
    });

    return (
        <ThemeProvider theme={mdTheme}>
        <Box sx={{ display: 'flex' }}>
            <CssBaseline />
            <AppBar position="absolute" open={open}>
                <Toolbar
                    sx={{
                    pr: '24px', // keep right padding when drawer closed
                    }}
                >
                    <IconButton
                        edge="start"
                        color="inherit"
                        aria-label="open drawer"
                        onClick={toggleDrawer}
                        sx={{
                            marginRight: '36px',
                            ...(open && { display: 'none' }),
                        }}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Box sx={{ flexGrow: 1, display: 'flex' }}>
                        <Typography
                            component="h1"
                            variant="h6"
                            color="inherit"
                            noWrap
                            sx={{paddingRight: "25px", marginTop: title==='Lattice' ? '3%': '0%'}}
                        >
                            {title}
                        </Typography>
                        {title==='Lattice' ? <NodeViewDropdown /> : <div></div>}
                    </Box>
                    <IconButton color="inherit" onClick={handleClickOpen}>
                        <SettingsIcon />
                    </IconButton>
                    <Dialog open={settingsOpen} onClose={handleClose}>
                        <DialogTitle sx={{backgroundColor:'#eee'}}>Settings</DialogTitle>
                        <DialogContent sx={{backgroundColor:'#eee'}}>
                            <Box component="form" sx={{ display: 'flex', flexWrap: 'wrap', backgroundColor:'#eee' }}>
                                    <Typography sx={{marginTop: "5%"}}>Verbose Mode: </Typography>
                                    <Checkbox 
                                        checked={verbose} 
                                        onChange={() => dispatch(toggleVerbose(!verbose))} 
                                        sx={{
                                            marginLeft: "5px"
                                        }}
                                    />
                            </Box>
                        </DialogContent>
                        <DialogActions sx={{backgroundColor:'#eee'}}>
                            <Button onClick={handleClose}>Close</Button>
                        </DialogActions>
                    </Dialog>
                </Toolbar>
            </AppBar>
            <Drawer variant="permanent" open={open} sx={{backgroundColor: '#eee'}}>
                <Paper sx={{
                    backgroundColor: '#eee',
                    height: '100vh'
                }}>
                    <Toolbar
                        sx={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'flex-end',
                        px: [1],
                        backgroundColor: '#eee'
                        }}
                    >
                        <IconButton onClick={toggleDrawer}>
                            <ChevronLeftIcon />
                        </IconButton>
                    </Toolbar>
                    <Divider />
                    <MainListItems saveNetworkImage={handleSaveImage} saveLatticeImage={handleSaveLatticeImage}/>
                    <div style={{position: 'absolute', bottom: '0px', display: 'flex', paddingLeft: '2px', visibility: running > 0 ? 'visible' : 'hidden'}}>
                        <CircularProgress sx={{color: '#BC4A09'}} size={15} value={70}/>
                        <Typography fontSize={12} sx={{paddingLeft: '3px', marginBottom: '4px'}}> Running </Typography> 
                    </div>
                </Paper>
            </Drawer>
            <Box
                component="main"
                sx={{
                    backgroundColor: (theme) =>
                    theme.palette.mode === 'light'
                        ? theme.palette.grey[100]
                        : theme.palette.grey[900],
                    flexGrow: 1,
                    height: '100vh',
                    overflow: 'hidden',
                }}
            >
                <Toolbar />
                <Paper
                    sx={{
                        p: 2,
                        display: 'flex',
                        flexDirection: 'column',
                        height: '92vh',
                        width: '100%',
                        overflow: title==='Organizations' ? 'auto' : 'hidden',
                        backgroundColor: '#eee',
                    }}
                >
                    <TabPanel value={title} index={'Editor'}>
                        <Dropzone />
                    </TabPanel>
                    <TabPanel value={title} index={'Reaction Network'}>
                        <ReactionNetwork ref={networkRef}/>
                    </TabPanel>
                    <TabPanel value={title} index={'Lattice'}>
                        <LatticeGraph ref={latticeRef}/>
                    </TabPanel>
                    <TabPanel value={title} index={'Organizations'} >
                        <OrganizationList />
                    </TabPanel>
                </Paper>
            </Box>
        </Box>
        </ThemeProvider>
    );
}
