import { GLPK, LP } from "glpk.js";
import { Organization, Reaction, Species } from "../../app/";

function contains(largeSet: number[], smallSet: number[]): boolean {
    for(let i = 0; i < smallSet.length; i++) {
        if(smallSet[i] > 0) {
            if(largeSet[i] !== 1) {
                return false;
            }
        }
    }
    return true;
}

export function generateOrganizations(
    semiOrganization: number[], 
    numberOfOrganizations: number,
    speciesList: Species[], 
    reactionList: Reaction[],
    glpk: GLPK
): Organization | undefined {
    let numberOfReactionsInvolved = 0;
    let entryCount = 1;

    for(let j = 0; j < reactionList.length; j++) {
        if(contains(semiOrganization.slice(1), reactionList[j].stoichiometry.slice(0, speciesList.length))) {
            numberOfReactionsInvolved++;
        }
    }
    if(numberOfReactionsInvolved > 0) {
        
        const lp: LP = glp_create_prob(glpk, 'linear_programming_problem');
        const ia: number[] = [];
        const ja: number[] = [];
        const ar: number[] = [];
        glp_add_cols(lp, numberOfReactionsInvolved);
        for(let j = 0  ; j < numberOfReactionsInvolved; j++) {
            glp_set_col_bnds(lp, j, glpk.GLP_LO, 1.0, 0.0);
            glp_set_obj_coef(lp, j, 0.0);
        }

        let species_position = 0;
        for(let j = 1; j <= speciesList.length; j++) {
            if(semiOrganization[j] !== 0) {
                let reac_pos = 0;
                

                for(let k = 0; k < reactionList.length; k++) {
                    if(contains(semiOrganization.slice(1), reactionList[k].stoichiometry.slice(0, speciesList.length))) {
                        
                        if(reactionList[k].stoichiometry.slice(0, speciesList.length) !== reactionList[k].stoichiometry.slice(speciesList.length)) {
                            entryCount++;
                            ia.push(species_position);
                            ja.push(reac_pos);
                            ar.push(reactionList[k].stoichiometry.slice(speciesList.length)[j-1] - reactionList[k].stoichiometry.slice(0, speciesList.length)[j-1]);
                        }
                        reac_pos++;
                    }
                }
                species_position++;
            }
        }
        glp_add_rows(lp, species_position);

        for(let j = 0; j < species_position; j++) {
            glp_set_row_bnds(lp, j, glpk.GLP_LO, 0.0, 0.0);
        }

        glp_load_matrix(lp, entryCount-1, ia, ja, ar);
        //console.log(lp);
        const result = glpk.solve(lp);
        let bool = !(result.result.status === 1 || result.result.status === 3 || result.result.status === 4)
        if(bool) {
            return generateOrgVar(semiOrganization, reactionList, speciesList, numberOfOrganizations);
        } else {
            return undefined;
        }
    }
    return generateOrgVar(semiOrganization, reactionList, speciesList, numberOfOrganizations);
}

function generateOrgVar(semiOrganization: number[], reactionList: Reaction[], speciesList: Species[], numberOfOrganizations: number): Organization {
    const org: Organization = {
        id: `O${numberOfOrganizations}`,
        species: [],
        reactions: reactionsOfInterest(semiOrganization, reactionList, speciesList),
        stoichiometry: generateStochiometriy(semiOrganization, reactionList, speciesList)
    };
    for(let i = 1; i < semiOrganization.length; i++) {
        if(semiOrganization[i] !== 0) {
            org.species.push(speciesList[i-1]);
        }
        
    }
    return org;
}

function generateStochiometriy(semiOrg: number[], reactionList: Reaction[], speciesList: Species[]): number[][] {
    let  matrix: number[][] = [];
    for (const reaction of reactionList) {
        const reactants = reaction.stoichiometry.slice(0,speciesList.length);
        const products = reaction.stoichiometry.slice(speciesList.length, speciesList.length*2);

        const row = [];
        for (let i = 0; i < reactants.length; i++) {
            row.push(products[i]-reactants[i]);
        }
        matrix.push(row);
    }
    matrix = transpose(matrix);
    return matrix;
}

function transpose(matrix: number[][]) {
    const rows = matrix.length, cols = matrix[0]?.length | 0;
    const grid = [];
    for (let j = 0; j < cols; j++) {
      grid[j] = Array(rows);
    }
    for (let i = 0; i < rows; i++) {
      for (let j = 0; j < cols; j++) {
        grid[j][i] = matrix[i][j];
      }
    }
    return grid;
}

function reactionsOfInterest(set: number[], reactionList: Reaction[], speciesList: Species[]): Reaction[] {
    const reactions: Reaction[] = [];
    for(const reaction of reactionList) {
        const educts = reaction.stoichiometry.slice(0,speciesList.length);
        if(contains(set.slice(1), educts)) {
            reactions.push(reaction);
        }
    }

    return reactions;
}


function glp_create_prob(glpk: GLPK, name: string): LP {
    const newLP: LP = {
        name: name,
        options: {
            presol: true,
            msglev: glpk.GLP_MSG_OFF
        },
        objective: {
            direction: glpk.GLP_MAX,
            name: 'obj',
            vars: [],
        },
        subjectTo: [],
        bounds: []
    };

    return newLP;
}

function glp_add_cols(lp: LP, count: number): void {
    for(let i = 0; i < count; i++) {
        lp.objective.vars.push({name: `r${i}`, coef: 0})
        lp.bounds?.push({name: `r${i}`, type: 0, ub: 0, lb: 0});
    }
}

function glp_add_rows(lp: LP, count: number): void {
    for(let i = 0; i < count; i++) {
        let vars: {name: string; coef: number}[] = new Array(lp.objective.vars.length);
        for(let j =0; j < vars.length; j++) {
            vars[j] = {name: `r${j}`, coef: 0};
        }
        lp.subjectTo.push(
            {
                name: `s${i}`, 
                vars: vars, 
                bnds: {type: 0, ub: 0, lb: 0}
            }
        );
    }
}

function glp_set_col_bnds(lp: LP, column: number, type: number, lb: number, ub: number): void {
    if(lp.bounds) {
        lp.bounds[column].type = type;
        lp.bounds[column].lb = lb;
        lp.bounds[column].ub = ub;
    }
}

function glp_set_row_bnds(lp: LP, row: number, type: number, lb: number, ub: number): void {
    lp.subjectTo[row].bnds.type = type;
    lp.subjectTo[row].bnds.lb = lb;
    lp.subjectTo[row].bnds.ub = ub;
}

function glp_set_obj_coef(lp: LP, object: number, coef: number): void {
    lp.objective.vars[object].coef = coef;
}

function glp_load_matrix(lp: LP, entryCount: number, ia: number[], ja: number[], ar: number[]): void {
    for(let i = 0; i < entryCount; i++) {
        lp.subjectTo[ia[i]].vars[ja[i]].coef = ar[i];
    }
}
