import FileSaver from 'file-saver';
import convert from 'xml-js';

import { Reaction, Species } from "../";
import MultiSet from '../classes/multiSet';

function SBMLReader (sbml: string): {
    species: {
        [key: string]: Species;
    };
    reactions: {
        [key: string]: Reaction;
    };
} {
    const speciesObject: { [key: string] : Species} = {};
    const reactionObject: { [key: string] : Reaction} = {};
    let speciesCount = 0;
    try {
        const json = JSON.parse(convert.xml2json(sbml, {compact: true, spaces: 4}));
        json.sbml.model.listOfSpecies.species.forEach((element: {_attributes: {id: string, name: string, compartment: string}}) => {
            const species: Species = {
                uid: element._attributes.id,
                id: element._attributes.name,
                index: speciesCount++,
                meta: {
                compartement: element._attributes.compartment
                }
            }
            speciesObject[`${species.uid}`] = species;
        });
        const speciesArray = Object.keys(speciesObject);
        let reactionCount = 0;
        json.sbml.model.listOfReactions.reaction.forEach((element: any) => {
            const reactants: Species[] = [];
            const products: Species[] = [];
            const stoichiometry: number[] = new Array(speciesArray.length*2).fill(0);

            if (element.listOfReactants) {
                try {
                    element.listOfReactants.speciesReference.forEach((reactant: {_attributes: {species: string, stoichiometry: string}}) => {
                        reactants.push(speciesObject[reactant._attributes.species]);
                        const index = speciesArray.findIndex(element => element === reactant._attributes.species);
                        stoichiometry[index] = reactant._attributes.stoichiometry ? Number.parseFloat(reactant._attributes.stoichiometry) : 1;
                    });
                } catch {
                    
                }

                if(reactants.length === 0) {
                    const reactant = element.listOfReactants.speciesReference;
                    reactants.push(speciesObject[reactant._attributes.species]);
                    const index = speciesArray.findIndex(element => element === reactant._attributes.species);
                    stoichiometry[index] = reactant._attributes.stoichiometry ? Number.parseFloat(reactant._attributes.stoichiometry) : 1;
                }
            }
            
            if (element.listOfProducts) {
                try {
                    element.listOfProducts.speciesReference.forEach((product: {_attributes: {species: string, stoichiometry: string}}) => {
                        products.push(speciesObject[product._attributes.species]);
                        const index = speciesArray.findIndex(element => element === product._attributes.species);
                        stoichiometry[speciesArray.length+index] = product._attributes.stoichiometry ? Number.parseFloat(product._attributes.stoichiometry) : 1;
                    });
                } catch {
                }
                if(products.length === 0) {
                    const product = element.listOfProducts.speciesReference;
                    products.push(speciesObject[product._attributes.species]);
                    const index = speciesArray.findIndex(element => element === product._attributes.species);
                    stoichiometry[speciesArray.length+index] = product._attributes.stoichiometry ? Number.parseFloat(product._attributes.stoichiometry) : 1;
                }
            }
            
            const speciesarr2str = (arr: Species[], offset: number) => {
                let str = '';

                arr.forEach(element => {
                    let index = speciesArray.findIndex(ele => ele === element.uid);
                    str = `${str} ${element.uid}[${stoichiometry[index + offset]}]`;
                });

                return str.trimStart();
            }

            const reaction: Reaction = {
                index: reactionCount++,
                uid: `${speciesarr2str(reactants, 0)} -> ${speciesarr2str(products, speciesArray.length)}`,
                reversable: JSON.parse(element._attributes.reversible),
                reactants: new MultiSet(reactants, speciesArray.length),
                products: new MultiSet(products, speciesArray.length),
                stoichiometry: stoichiometry,
            }
            reactionObject[`${reaction.uid}`] = reaction;
        });
    } catch (error: any){
        console.warn(error);
    }
    return {species: speciesObject, reactions: reactionObject};
}

export default SBMLReader;

export function SBMLWriter (xml: string) {
    let blob = new Blob([xml], {
        type: "text/plain;charset=utf-8"
    });
    FileSaver.saveAs( blob, 'Network.xml' );
}
