import * as Comlink from 'comlink';
import { EdgeDefinition, NodeDefinition } from 'cytoscape';
import { setNetworkGraph, setRunning } from '../../app/state/appState';
import { store } from '../../app/store';
import networkAPI, { NetworkWorker } from './network.worker';

export default function generateReactionNetwork() {
    store.dispatch(setRunning(1));
    const state = store.getState().network_reducer;
    const species = Object.keys(state.species);
    const reactionList = new Int8Array(Object.values(state.reactions).map(reaction => Array.from(reaction.stoichiometry)).flat());
    const worker: Comlink.Remote<NetworkWorker> = Comlink.wrap(new networkAPI());
    worker.generateGraph(species, reactionList.buffer, Comlink.proxy(callback));
}

function callback(nodes: NodeDefinition[], edges: EdgeDefinition[], error: any | undefined): void {
    if(error) {
        alert(error + '\n\nCheck the console for more information.');
    } else {
        store.dispatch(setNetworkGraph([...nodes, ...edges]));
        store.dispatch(setRunning(-1));
    }
    
}
